.articles-list {
  padding-bottom: 160px;
  // .articles-list__navigation
  &__navigation {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 56px;
    // .articles-list__navigation-btn
    &-btn {
      display: flex;
      padding: 0;

      background: none;
      border: none;
      svg{
        fill: $primary-color;
      }
      
      // .articles-list__navigation-btn_inactive
      &_inactive {
        opacity: 0.3;
      }
      
      // .articles-list__navigation-btn_next
      &_next {
        margin-left: 20px;
      }
    }
  }
  // .articles-list__item
  &__item {
    width: 100%;
  }
}

.articles-list-item {
  // .articles-list-item__image
  &__image {
    display: flex;
    width: 100%;
    margin-bottom: 19px;
    picture{
      width: 100%;
    }
    img{
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }

  // .articles-list-item__description
  &__description {
    color: $text-color;
    line-height: 160%;
    text-decoration: none;
  }

  // .articles-list-item__bottom
  &__bottom {
    justify-content: space-between;
    width: 100%;
    margin-top: 46px;
  }

  // .articles-list-item__info
  &__info {

    // .articles-list-item__info-category
    &-category {
      margin-left: 8px;
      
      color: $primary-color;
      font-weight: 400;
      text-transform: uppercase;
      text-decoration: none;
    }
    // .articles-list-item__info-line
    &-line {
      display: block;
      width: 1px;
      height: 11px;
      margin-left: 12px;

      background: $text-color;
    }
  }


  // .articles-list-item__next
  &__next {
    display: flex;
    padding: 0;

    background: none;
    border: none;
  }
}


// <1280
@include media-laptop {
  .articles-list{
    padding-bottom: 80px;
  }
}
// <992
@include media-tablet-horizontal {
  .articles-list{
    padding-bottom: 60px;
  }
}
// <768
@include media-tablet {
  .articles-list{
    padding-bottom: 30px;
    &__navigation{
      margin-bottom: 20px;
    }
  }
}
// <480
@include media-mobile {}
// <320
@include media-mobile-small {}