.selecting-articles {
  padding: 40px 0 54px;

  // .selecting-articles__tabs
  &__tabs {
    flex-wrap: wrap;
    margin-top: 40px;
  }

  // .selecting-articles__tab
  &__tab {
    // .selecting-articles__tab_active
    flex: 1 1 200px;
    margin-right: 106px;
    padding: 10px 5px;
    /* identical to box height */

    color: $text-color;
    font-weight: 350;
    line-height: 21px;

    background: none;
    border: none;
    border-bottom: 1px solid #F5F5F5;
    &:last-child {
      margin-right: 0;
    }
    &_active {
      color: $primary-color;

      border-color: $primary-color;
    }
  }

  // .selecting-articles__content
  &__content {
    display: none;
    margin-top: 40px;

    opacity: 0;
    &_active {
      display: flex;

      opacity: 1;
    }
  }

  // .selecting-articles__list
  &__list {
    display: grid;
    grid-template-areas: 'primary item';
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr;
    gap: 0px 48px;
    align-content: space-between;
    align-items: stretch;
    &_primary {
      grid-area: primary;
      padding-right: 48px;

      border-right: 1px solid #F5F5F5;
      .selecting-articles-item {
        &__image {
          max-height: 563px;
          margin-bottom: 23px;
        }
        // .selecting-articles-item__title
        &__title {
          margin-bottom: 16px;
        }
        // .selecting-articles-item__description
        &__description {
          margin-bottom: 23px;
        }
        &__info {
          &-line {
            margin-left: 23px;
          }
          .post-date {
            &__day {
              font-size: 24px;
            }
            &__month,
            &__line {
              font-size: 14px;
            }
          }
          &-category {
            font-size: 14px;
          }
        }
      }
    }
    &_items {
      display: grid;
      grid-area: item;
      grid-template-rows: repeat(2, 1fr);
      grid-template-columns: 1fr;
      gap: 0px 0px;
      // padding-left: 48px;
      .selecting-articles__item {
        &:first-child {
          padding-bottom: 28px;

          border-bottom: 1px solid #F5F5F5;
        }
        &:last-child {
          padding-top: 25px;
        }
      }
      .selecting-articles-item {
        &__image {
          margin-bottom: 13px;
        }
        // .selecting-articles-item__title
        &__title {
          margin-bottom: 9px;
        }
        // .selecting-articles-item__description
        &__description {
          margin-bottom: 15px;
        }
        &__info {
          &-category {
            font-size: 12px;
          }
        }
      }
    }
  }
}

.selecting-articles-item {
  display: flex;
  flex-direction: column;
  height: 100%;
  // .selecting-articles-item__image
  &__image {
    display: flex;
    overflow: hidden;
    img,
    picture {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  // .selecting-articles-item__title
  &__title {
    font-weight: 400;
    line-height: 160%;
  }

  // .selecting-articles-item__description
  &__description {
    font-weight: 350;
    line-height: 160%;
  }

  // .selecting-articles-item__bottom
  &__bottom {
    justify-content: space-between;
    width: 100%;
  }

  // .selecting-articles-item__info
  &__info {
    // .selecting-articles-item__info-category
    &-category {
      margin-left: 8px;

      color: $primary-color;
      font-weight: 400;
      text-transform: uppercase;
      text-decoration: none;
    }
    // .selecting-articles-item__info-line
    &-line {
      display: block;
      width: 1px;
      height: 11px;
      margin-left: 12px;

      background: $text-color;
    }
  }

  // .selecting-articles-item__next
  &__next {
    display: flex;
    padding: 0;

    background: none;
    border: none;
  }
}

// <1280
@include media-laptop {
  .selecting-articles {
    padding: 40px 0;
    &__tabs {
      margin-top: 30px;
    }
  }
}
// <992
@include media-tablet-horizontal {
  .selecting-articles {
    padding: 30px 0 50px;
    &__tab{
      flex-basis: calc(25% - 20px);
      margin-right: 20px;
    }
  }
}
// <768
@include media-tablet {
  .selecting-articles{
    &__tabs{
      margin-top: 20px;
    }
    &__list{
      grid-template-areas: 
        "primary" 
        "item";
      grid-template-rows: 1fr;
      grid-template-columns: 1fr;
      gap: 30px 0px;
      &_primary{
        padding-right: 0; 

        border-right: none;
      }
      &_items{
        grid-template-rows: 1fr;
        padding-bottom: 0;
        .selecting-articles__item:first-child{
          padding-bottom: 20px;
        }
      }
    }
  }
}
// <480
@include media-mobile {
  .selecting-articles{
    &__tab{
      flex-basis: 100%;
      margin: 0;
    }
  }
}
// <320
@include media-mobile-small {
}
